import BScroll from '@better-scroll/core'
import Slide from '@better-scroll/slide'
import { onMounted, onUnmounted, ref } from 'vue'

BScroll.use(Slide)

export function useSlider(wrapperRef) {
  const slider = ref(null)
  const currentPageIndex = ref(0)
  //dom
  onMounted(() => {
    const silderVal = (slider.value = new BScroll(wrapperRef.value, {
      click: true,
      scrollX: true,
      scrollY: false,
      momentum: false,
      bounce: false,
      probeType: 2,
      slide: true
    }))
    silderVal.on('slideWillChange', (page) => {
      currentPageIndex.value = page.pageX
    })
    console.log('slidervalue', slider.value)
  })
  //
  onUnmounted(() => {
    console.log('onUnmounted')
    // slider.value.destory()
    // slider.value.hooks.on('destroy', () => {
    //   console.log('destroyed')
    // })
  })

  return {
    slider,
    currentPageIndex
  }
}
